import { Component, h, Prop, State, Element } from '@stencil/core';
import { DialogChoiceData, DialogData, guide, GuideData } from "../../../global/data/guide";
import { jsonfetch } from '../../../global/functions/fetch';
import { marked } from 'marked';

declare global { interface Window { dataLayer: any[] }}
@Component({
    tag: 'ks-page-guide',
    styleUrl: 'guide.css'
})
export class PageGuide {
    @Element() root: HTMLElement;
    @Prop() skipbase: boolean;
    @Prop() commonData: string;
    @Prop() commonDynamicData: string;

    @Prop() guideData: string;
    @Prop({mutable: true, reflect: true}) loading: boolean = false;

    @State() active: string = null;
    @State() summary: boolean = false;
    @State() choices: {category: string, choices: string[]}[] = [];

    componentWillLoad() {
        const guideDataElement = document.getElementById(this.guideData);
		const guideData = JSON.parse(guideDataElement.innerHTML) as GuideData;

        Object.keys(guideData).map(key => {
			guide.set(key as keyof GuideData, guideData[key]);
		});

        this.resetActive();
    }

    initialCategory() {
        return guide.get("dialog")?.[0]?.id;
    }

    resetActive() {
        this.active = this.initialCategory();
    }

    nextCategory() {
        this.selectCategoryRelative(1);
    }
    
    previousCategory() {
        this.selectCategoryRelative(-1);
    }

    selectCategoryRelative(distance: number) {
        const category = guide.get("dialog")?.[this.activeIndex() + distance];
        this.selectCategory(category?.id);
    }

    activeIndex() {
        return this.index(this.active);
    }

    index(category: string) {
        return guide.get("dialog")?.findIndex(item => item.id == category);
    }

    activeCategory() {
        return this.category(this.active);
    }

    category(category: string) {
        return guide.get("dialog")?.find(item => item.id == category);
    }

    activeCategoryChoices() {
        return this.categoryChoices(this.active);
    }

    categoryChoices(category: string) {
        return this.choices.find(item => item.category == category)?.choices;
    }

    selectChoice(category: string, choice: DialogChoiceData) {
        let choices = this.choices;
        const id = choice?.id ?? null;
        const multiple = this.category(category)?.multiple;

        const current = this.choices.find(item => item.category == category);

        if(current) {
            if(multiple) {
                const pos = current.choices.indexOf(id);
                if(pos != -1) current.choices.splice(pos, 1);
                else current.choices.push(id);
            }
            else current.choices = [id];
        }
        
        else {
            this.choices.push({
                category: category,
                choices: [id]
            });
        }

        this.choices = [...choices];
        if(choice == null) this.nextCategory();
    }

    selectCategory(category: string) {
        const previousIndex = this.activeIndex();
        const index = this.index(category);

        if(index == -1)
            this.summary = true;

        else {
            if(this.choices.length > index)
                this.choices = [...this.choices.slice(0, index)];

            this.active = category;
            this.summary = false;
        }

        if(this.active == this.initialCategory())
            this.componentWillLoad();

        else this.dynamicResults(previousIndex);

        this.scroll();
    }

    scroll() {
        const dialog = this.root.querySelector(".dialog");
        const nav_rect = dialog.querySelector(".navigation")?.getBoundingClientRect();

        const in_view = nav_rect.top >= 0 &&
            nav_rect.left >= 0 &&
            nav_rect.bottom <= (window.innerHeight || document.documentElement.clientHeight) &&
            nav_rect.right <= (window.innerWidth || document.documentElement.clientWidth);

        if(!in_view) setTimeout(() => {
            const y = dialog?.getBoundingClientRect()?.top + window.scrollY;
            window.scroll({top: y, behavior: 'smooth'});
        }, 300);
    }

    reset() {
        this.summary = false;
        this.resetActive();
        this.choices = [];
        this.componentWillLoad();
    }

    async dynamicResults(previousIndex) {
        this.loading = true;

        const data = await jsonfetch(guide.get("api"), {
            "choices": this.choices
        });
        const guideData = await data?.json();

        if(guideData) {
            guide.set("dialog", guideData?.dialog);
            guide.set("products", guideData?.products);
        }

        this.loading = false;

        if(!this.activeCategory()) {
            const category = guide.get("dialog")?.[previousIndex+1];

            if(category) this.active = category.id;
            else this.summary = true;
        }
    }

	render() {
        const dialog = guide.get("dialog");
        const products = guide.get("products");

		return <ks-page-base skipbase={this.skipbase} commonData={this.commonData} commonDynamicData={this.commonDynamicData}>
            <div class="content">
                <div class="text">
                    <h1>{guide.get("heading")}</h1>
                    <p>{guide.get("description")}</p>
                </div>

                <div class="dialog">
                    <div class="navigation">
                        <div class="breadcrumbs">
                            {dialog.slice(0, Math.max(this.choices.length, this.activeIndex())).map(category =>
                                <div onClick={()=>this.selectCategory(category.id)}>{category?.name}</div>
                            )}
                        </div>
                        <div class="back" onClick={()=>this.previousCategory()}>
                            <ks-icon name="chevron-left" />
                        </div>
                        <div>{this.summary ? dialog.length : this.activeIndex()+1} z {dialog.length}</div>
                    </div>
                    
                    {this.summary ? this.summary_stage(dialog) : this.choices_stage()}
                </div>

                {products && products?.length > 0 && (this.activeIndex() > 0 || this.summary) ?
                    <div class="products">
                        {products?.map((product, index) => 
                            <div class="product" key={product.id}>
                                {index == 0 ? <div class="best">Najlepszy wybór</div> : null}
                                <ks-product-card constheight flat linkOnly product={product} />
                                {product?.conditions?.map(category =>
                                    <div class="condition"> 
                                        <ks-icon name="check-circle" />
                                        {category.category}: {category.items.join(", ")}
                                    </div>
                                )}
                            </div>
                        )}
                    </div>
                : null}
                
                <div class="text" innerHTML={marked.parse(guide.get("bottom_description"))} />
            </div>
        </ks-page-base>;
	}

    choices_stage = () => [
        <h2>{this.activeCategory()?.heading}</h2>,
        <div class={["choices", this.activeCategory()?.multiple ? "multiple" : null].join(" ")}>

            {this.activeCategory()?.choices.map(choice => 
                <div class={["choice", this.activeCategoryChoices()?.includes(choice.id) ? "active" : null].join(" ")}
                    onClick={()=>this.selectChoice(this.active, choice)}
                >
                    <ks-img3 fit='cover' image={choice.image} class={choice.description ? "large" : null}/>
                    <div class="description">
                        <h3>{choice.name}</h3>
                        {choice.description ? <p>{choice.description}</p> : null}
                    </div>
                    <div class="select" />
                </div>
            )}

            {this.loading}

        </div>,
        <div class="buttons">
            {this.activeCategory()?.nopreference ? 
                <ks-button round border name="Nie mam preferencji" onClick={()=>this.selectChoice(this.active, null)} />
            : null}

            {this.activeCategoryChoices()?.length > 0 ? 
                <ks-button round name="Kontynuuj" onClick={()=>this.nextCategory()} />
            : null}
        </div>
    ];

    summary_stage = (dialog: DialogData[]) => [
        <h2>Dziękujemy za Twoją opinię!</h2>,
        <p>Na podstawie Twoich odpowiedzi możemy polecić Ci kilka świetnych rozwiązań. Sprawdź je poniżej.</p>,
        <div class="summary">
            {dialog?.map((category) => this.categoryChoices(category.id)?.includes(null) ? null :
                <div>{category.name}: {
                    this.categoryChoices(category.id)?.map(id =>
                        category.choices?.find(choice => choice.id == id)?.name
                    )?.map(name => <div>{name}</div>)
                }</div> 
            )}
            <ks-button name="Rozpocznij ponownie" icon="edit-3" onClick={()=>this.reset()}/>
        </div>
    ];
}




