import { createStore } from "@stencil/store";
import { ProductCardData } from "./listing";

export const guide = createStore({} as GuideData);

export interface GuideData {
    dialog?: DialogData[];
    api: string;
    products?: GuideProductData[];
    heading: string;
    description: string;
    bottom_description: string;
}

export interface DialogData {
    id: string;
    name: string;
    heading: string;
    choices: DialogChoiceData[];
    multiple: boolean;
    nopreference: boolean;
}

export interface DialogChoiceData {
    id: string;
    name: string;
    description: string;
    image: string;
}

export interface GuideProductData extends ProductCardData {
    conditions: GuideProductCategory[];
}

export interface GuideProductCategory {
    category: string;
    items: string[];
}

