ks-page-guide { 
    display: block;
    background-color: #f2f2f2;
}

ks-page-guide .text { 
    max-width: 1000px;
    padding: 20px;
    margin: auto;
}

ks-page-guide .dialog { 
    max-width: 1000px;
    width: 100%;
    padding: 20px;
    margin: auto;
    box-sizing: border-box;

    background-color: #ffffff;
    border: 1px solid #e9e9e9;
    border-radius: 10px;

    overflow: hidden;
}

ks-page-guide .dialog .buttons {
    display: flex;
    justify-content: flex-end;
    gap: 10px;
    margin-top: 10px;
}

ks-page-guide .dialog .choices {
    display: flex;
    gap: 10px;
    flex-wrap: wrap;
}

ks-page-guide .dialog .choice {
    width: 100%;
    max-width: 215px;
    background-color: #f2f2f2;
    border-radius: 10px;
    overflow: hidden;
    position: relative;
    padding: 8px;
    cursor: pointer;
}

ks-page-guide .dialog .choice ks-img3 {
    height: 140px;
    border-radius: 10px;
    overflow: hidden;
}

ks-page-guide .dialog .choice .description {
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
}

ks-page-guide .dialog .choice h3 {
    font-size: 17px;
    padding: 10px 10px 6px 10px;
    margin: 0;
}

ks-page-guide .dialog .choice p {
    padding: 0px 10px 10px 10px;
    margin: 0;
    font-size: 14px;
}

ks-page-guide .dialog .choice .select {
    position: absolute;
    top: 4px;
    right: 4px;
    width: 35px;
    height: 35px;
    background-color: #f2f2f2;
    border-radius: 35px 0 35px 35px;
}

ks-page-guide .dialog .choice .select::before {
    content: "";
    display: block;
    width: 25px;
    height: 25px;

    position: absolute;
    right: 5px;
    top: 5px;

    border-radius: 25px;
    border: 1px solid #818181;
    box-sizing: border-box;
}

ks-page-guide .dialog .choice.active .select::after {
    content: "";
    display: block;
    width: 17px;
    height: 17px;
    position: absolute;
    
    right: 9px;
    top: 9px;
    border-radius: 17px;
    background-color: #212121;
}

ks-page-guide .dialog .choices.multiple .choice .select {
    border-radius: 0 0 0 8px;
    top: 8px;
    right: 8px;
    width: 30px;
    height: 30px;
}

ks-page-guide .dialog .choices.multiple .choice .select::before {
    right: 0px;
    top: 0px;
    border-radius: 5px;
}

ks-page-guide .dialog .choices.multiple .select::after {
    right: 4px;
    top: 4px;
    border-radius: 3px;
}

ks-page-guide .dialog .navigation {
    display: flex;
    justify-content: space-between;
    margin-bottom: 10px;
}

ks-page-guide .dialog .navigation .breadcrumbs {
    display: flex;
    gap: 5px;
}

ks-page-guide .dialog .navigation .breadcrumbs > * {
    background-color: #dddddd;
    padding: 5px 10px;
    border-radius: 7px;
    cursor: pointer;
}

ks-page-guide .dialog .navigation .back {
    background-color: #dddddd;
    padding: 5px 10px;
    border-radius: 7px;
    cursor: pointer;
}

ks-page-guide .products {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    gap: 10px;
    
    width: 100%;
    margin: 20px auto 0 auto;
    padding: 0 10px;
    box-sizing: border-box;
}

ks-page-guide .products .product {
    position: relative;
    max-width: 300px;
    flex: 1 1 260px;
}

ks-page-guide .products .product ks-product-card {
    width: 100%;
    margin-bottom: 10px;
}

ks-page-guide .products .product .condition {
    padding: 5px 0;
    font-size: 14px;
}

ks-page-guide .products .product .condition ks-icon {
    margin-right: 5px;
}

ks-page-guide .products .product .best {
    position: absolute;
    z-index: 1;
    top: 10px;
    left: -5px;
    background-color: #151515;
    color: #ffffff;
    padding: 5px 15px;
    border-radius: 5px;
}

ks-page-guide .summary > div {
    margin: 0 0 10px 0;
}

ks-page-guide .summary > div > div {
    display: inline-block;
    background-color: #dddddd;
    padding: 3px 7px;
    border-radius: 5px;
    margin: 2px;
}

ks-page-guide .summary ks-button {
    margin: 20px -20px -20px -20px;
}


ks-page-guide .dialog .choice,
ks-page-guide .dialog .navigation .breadcrumbs > * {
    transition: opacity 0.3s ease;
    opacity: 1;
}

ks-page-guide .dialog .navigation .breadcrumbs > *:hover {
    opacity: 0.8;
}

ks-page-guide .dialog .choice:hover {
    opacity: 0.95;
}

ks-page-guide .dialog .choice:hover,
ks-page-guide .dialog .navigation .breadcrumbs > *:active {
    opacity: 0.7;
}

ks-page-guide[loading] .dialog .choices,
ks-page-guide[loading] .products {
    opacity: 0;
    transition: none;
}

ks-page-guide .dialog .choices,
ks-page-guide .products {
    transition: opacity 0.3s ease;
}

ks-page-guide .dialog .navigation .back {
    display: none;
}

@media screen and (max-width: 1010px) {
    ks-page-guide .dialog .navigation .breadcrumbs {
        display: none;
    }

    ks-page-guide .dialog .navigation .back {
        display: block;
    }

    ks-page-guide .dialog .choices .choice {
        display: flex;
        align-items: center;
        width: 100%;
        max-width: 100%;
    }

    ks-page-guide .dialog .choice ks-img3 {
        height: 50px;
        width: 80px;
    }

    ks-page-guide .dialog .choice ks-img3.large {
        height: 80px;
        width: 80px;
    }

    ks-page-guide .dialog .choices .choice .select {
        position: relative;
        top: 0;
        right: 0;
    }
    
    ks-page-guide .dialog .choices.multiple .choice .select {
        position: relative;
        top: 2.5px;
    }
    
    ks-page-guide .product .condition {
        padding: 2px 0;
    }

    ks-page-guide .product .condition ks-icon {
        transform: scale(0.9);
        margin: 0;
    }
}

@media screen and (min-width: 701px) {
    ks-page-guide > ks-page-base > .content { 
        padding: 0 40px;
    }
}